@use 'variables' as v;
@use 'mixins' as m;
html {
    font-size: 62.5%;
    box-sizing: border-box;
    scroll-padding-top: 0rem;
    /* font-family: "Exo 2", sans-serif; */
    font-optical-sizing: auto;
    font-style: normal;
}
*, 
*:before,
*:after {
    box-sizing: inherit;
}

body{
    min-height: 100vh;
    margin: 0;
    background-color: #B74E6A;
    position: relative;
}
main{
    margin-bottom: 5rem;
}
.contenedor {
    width: 95%;
    max-width: 100rem;
    margin: 0 auto;
}
img {
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}
a{
    text-decoration: none;
}

.displayNone{
    display: none!important;
}

:root{
    --separacion: .8rem;
    --separacionGrande: 16rem;
}  
/* Globales parte 2 */


.subrayarAmarillo{
    color: rgb(182, 182, 19)!important;
}
.ocultar{
    display: none!important;
}
.mostrar{
    display: block!important;
}
.field{
    margin-top: 2rem;
}

legend{
    font-size: 2rem;
    font-weight: bold;
}
fieldset{
    padding: 2rem;
    margin: 0 2rem 2rem 2rem;
}
textarea{
    min-height: 10rem;
    max-width: auto;
    resize: none;
    font-size: 2rem;
    border-radius: 1rem;
}
.section{
    margin-bottom: 6rem;
    
}
.poisition-relative{
    position: relative;
}
.boton{
    @include m.boton(v.$rosadoOscuro, v.$azulOscuro);
    
}
.botonNegro{
    @include m.boton(v.$negro, v.$blanco)
    
}
.botonBorrar{
    @include m.boton(v.$rojo, v.$blanco);
}
.botonChico{
    width: 3rem;
    height: 3rem;
    background-color: v.$verde;
    font-size: 3rem;
    text-align: center;
    margin: 0;
    padding: 0;
    font-weight: bolder;
    cursor: pointer;
}
.botonChicoRojo{
    width: 3rem;
    height: 3rem;
    background-color: v.$rojo;
    font-size: 3rem;
    text-align: center;
    margin: 0;
    padding: 0;
    font-weight: bolder;
    cursor: pointer;
}
.botonComprar{
    @include m.boton(v.$azulClaro, v.$rosadoOscuro);
}
.botonAzul{
    @include m.boton(v.$azul, v.$blanco);
    margin: 3rem 1rem 1rem 0;
    
}
.hidden{
    visibility: hidden;
}